
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap'); 

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;

}
 
  
body {
    font-family: 'Poppins', 'sans-serif';

    position: relative;
    padding-bottom: 200px;
    min-height: 100vh;

}

.container {
    max-width: 95%;
    margin: 0 auto;
}

.header {
    display: flex;
    align-items: center;
    min-height: 70vh;
    position: relative;
}

/* MENU */

.menu {
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 1000;

    position: fixed;
    width: 100%;
    background-color: #ffffff; /* Color de fondo deseado */
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1); /* Agrega sombra si lo deseas */
 

}


.logo {
    font-size: 25px;
    color: #6775f8;
    text-transform: uppercase;
    font-weight: 800;
}

.menu .nav_menu ul li {
    position: relative;
    display: inline-block;
    margin-right: 20px;
    
}

.menu .nav_menu ul li a{
    font-size: 14px;
    padding: 20px;
    font-weight: 600;
    color: #1d1d1b;
    display: block;

}

.menu .nav_menu ul li a:hover {
    background-color: #b5bbff;
}


.menu_icono {
    width: 25px; 
}

/* esconder icono menu */
.menu label {
    cursor: pointer;
    display: none;
}


/* HEADER */

.conteiner_header {
    display: flex;
    align-items: center;
}

.txt_header {
    width: 50%;
    margin-right: 50px;
}

.txt_header h1 {
    font-size: 55px;
    line-height: 65px;
    color: #1d1d1b;
    margin-bottom: 35px;
}

.txt_header p {
    font-size: 16px;
    color: #5e5e5e;
    margin-bottom: 45px;
}

.txt_header span {
    color: #6775f8;
}

.btn_1 {
    display: inline-block;
    padding: 13px 45px;
    border-radius: 25px;
    background-color: #202447;
    font-size: 16px;
    color: #ffffff;
    text-transform: capitalize;
    box-shadow: 0 0 20p rgb(0, 0, 0);
}

.btn_1:hover {
    background-color: #b5bbff;
}

.img_header img {
    width: 650px;
    margin-bottom: 10px;
    border-radius: 50%; 
    border: 2px solid #fff; 
}

/* NOTICIAS */


.noticias {
    padding: 70px 0;
    text-align: center;
}

.noticias_container {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
}

.noticias_2 {
    display: flex;
    flex-direction: column;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    height: 350px; /* Establece una altura fija */
    width: 250px;
    box-sizing: border-box;
    margin-bottom: 20px; /* Agrega margen inferior para separar las noticias */
}


.section_1 {
    display: flex;
    align-items: center;
    background-color: #ef8700;
    padding: 10px;
    border-radius: 15px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);

}

.section_1 h3 {
    font-size: 15px;
    color: #ffffff;
    font-weight: 600;
    margin-bottom: 10px; /* Agregamos un margen inferior al título */
}

.section_1 img {
    width: 90px;
    margin-bottom: 10px;
}

.noticias_2 p {
    font-size: 14px;
    color: #2c3e50;
    text-align: center;
    margin: 10px; /* Agregamos margen inferior al párrafo */
    
    overflow-y: auto; /* Habilita el desbordamiento vertical y muestra una barra de desplazamiento si es necesario */

}

.btn_noticia {
    padding: 5px 25px;
    background-color: #FF9000;
    color: #ffffff;
    border-radius: 10px;
    text-align: center;
    display: block;

    margin-top: auto; /* Alineamos el párrafo en la parte inferior */
}


/* BOTON DE CARGAR MAS */
.btn_3 {
    
    background-color: #FF9000;
    margin-top: 50px;
    display: inline-block;
    padding: 13px 35px;
    border-radius: 25px;
    color: #ffffff;
    font-size: 16px;
    cursor: pointer;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.btn_3:hover {
    background-color: #b8b4ae;
}


/* INFORMACION */


.informacion {
    padding: 0 0 50px 0;
    text-align: center;
}

.informacion_container {
    margin-top: 55px;
    display: grid;
    gap: 20px;
}

.informacion_container_2 {    
    border: 2px  solid #000000;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 5px 10 px rgba(0, 0, 0, 0.2);
}

.Informacion_2 p {
    font-size: 15px;
    color: #FF9000;
    padding: 30px;
}
.Informacion_2 h3 {
    font-size: 22px;
    color: #000000;
}
.Informacion_2 img {
    max-width: 90%;
}

.btn_info {
    display: inline-block;
    padding: 5px 25px;
    background-color: #FF9000;
    color: #ffffff;
    border-radius: 10px;
    
}

.btn_info:hover {
    background-color: #3c3a38;
}


/*Footer */ 

.footer {
    
    padding: 50px 0;
	background-color: #7f82a0;
	font-family: 'Open Sans', sans-serif;
	font-size: 14px;
	text-align: center;

    position: absolute;
    width: 100%;
    bottom: 0;
}

  
    
.footer p {
	margin-top: 30px;
	font-size: 16px;
	text-align: center;
	color: #ffffff;
  }

.firmas {
	font-size: 10px;
	text-align: center;
	color: #ffffff;
  }
  
.footer ul {
	list-style: none;
  }

.footer ul li {
	display: inline-block;
	margin-right: 20px;
	color: #ffffff;
  }
  

/* Seccion - alumnos, docentes y acuerdos*/

.seccion {
	width: 90%; /* Ancho de cada sección */
	margin: 10px auto; /* Margen entre las secciones */
	padding: 10px; /* Espaciado interno de cada sección */
	background-color: #dfdfdf; /* Color de fondo de cada sección */
	border: 2px solid #ccc; /* Borde de 2px sólido en color gris */
	border-radius: 10px;
	box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);

	text-align: center;
  
}

.seccion_menu{
    padding: 80px;
}


.btn {
    display: inline-block;
    padding: 13px 45px;
    border-radius: 25px;
    background-color: #FF9000;
    font-size: 16px;
    color: #ffffff;
    text-transform: capitalize;
    box-shadow: 0 0 20p rgb(0, 0, 0);
    margin: 10px;
    cursor: pointer;
}

.btn:hover{
    background-color: #929190;
}

/* TABLAS DE PROGRAMAS */

.tabla-programas { 
    width: 100%;
    margin: 10px;
    border: 2px solid #ccc;
    border-collapse: collapse;
    box-shadow: 2px 2px 5px rgba(11, 0, 43, 0.3);
  
}

.contenedor-tablas { /* para que este una al lado de la otra*/
    display: flex;
    justify-content: center;
    margin: 10px;
}
  
.tabla-programas td, .tabla-programas th {
    padding: 5px;
    font-size: 15px; /* Tamaño de fuente de las celdas de la tabla */  
}

.tabla-programas caption {
    font-weight: bold;
    font-size: 20px;
    padding: 7px;
    background-color: #f5f5f5;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }

  .tabla-programas th {
    background-color: #333;
    color: white;
  }
  
  .tabla-programas td {
    text-align: center;
    padding: 10px;
    
  }
  
.tabla-programas tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .tabla-programas tr:hover {
    background-color: #8e91a0;
    cursor: pointer;
  }  


  .links button {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 15px;
    font-weight: bold;
  
    
    align-items: center;
  }
  
  .links button:hover {
    background-color: #8692CD;
  }
  
/* Tablas docentes */


.mi-tabla {	
    width: 100%;
    margin: 10px;
    border: 2px solid #ccc;
    border-collapse: collapse;
    box-shadow: 2px 2px 5px rgba(11, 0, 43, 0.3);

}

.mi-tabla caption {
  font-weight: bold;
  font-size: 20px;
  padding: 7px;
  background-color: #f5f5f5;
  border: 2px solid #ccc;
  box-shadow: 2px 2px 5px rgba(11, 0, 43, 0.3);
}

.mi-tabla td {  
    padding: 10px;
    font-size: 15px; /* Tamaño de fuente de las celdas de la tabla */  
	text-align: center;
  }

.mi-tabla th {
    padding: 10px;
    font-size: 15px; /* Tamaño de fuente de las celdas de la tabla */  
	text-align: center;
    background-color: #939292;
}
  
    
.mi-tabla tr:nth-child(even) {
	background-color: #ffffff;
  }
  
.mi-tabla tr:hover {
	background-color: #a6a6a8;
    cursor: pointer;
  }

.linksD button{    
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 15px;
  font-weight: bold;

  align-items: center;
}

.linksD button:hover {
    background-color: #032dff;
}

/* frame */
.frame {
    padding: 15px;

}


